<template>
  <Header></Header>
  <div>
    <div class="content-section introduction">
      <div class="feature-intro">
        <h1>User control</h1>
      </div>
    </div>

    <div class="content-section implementation">
      <div class="card">
        <DataTable :value="users" responsiveLayout="scroll">
          <Column field="id" header="Id"></Column>
          <Column field="username" header="Name"></Column>
          <Column field="email" header="Email"></Column>
          <Column field="last_login_ip" header="Last login ip"></Column>
          <Column field="last_login_time" header="Last login time"></Column>
          <Column header="Roles" :exportable="false" style="min-width: 8rem">
            <template #body="slotProps">

              <div v-for="role in slotProps.data.roles" :key="role">
              <Chip>
                {{ role }}
              </Chip>
            </div>

         

            </template>
          </Column>
          <Column v-if=" hasRole(['admin'])" :exportable="false" style="min-width: 8rem">
            <template #body="slotProps">
              <Button
                icon="pi pi-pencil"
                class="p-button-rounded p-button-success mr-2"
                @click="$refs.myChild.openModal(slotProps.data, awilableRoles)"
              />
              <Button
                icon="pi pi-trash"
                class="p-button-rounded p-button-warning"
                @click="$refs.deleteUser.openModal(slotProps.data.id)"
              />
            </template>
          </Column>
        </DataTable>
      </div>
    </div>
  </div>
  <editUser ref="myChild"></editUser>
  <deleteUser ref="deleteUser"></deleteUser>
</template>

<script>
import { list, rolesList } from "@/api/auth.js";

import DataTable from "primevue/datatable";
import Column from "primevue/column";
import Button from "primevue/button";
import Chip from "primevue/chip";

import {hasRole} from "@/service/user.js"
import Header from "@/components/header.vue";
import editUser from "@/components/modal/user/edit.vue";
import deleteUser from "@/components/modal/user/delete.vue";

import moment from "moment";

export default {
  components: {
    DataTable,
    Column,
    Button,
    Chip,

    editUser,
    deleteUser,
    Header,
  },
  methods: {
    hasRole,
   async getUsers(){
    let users = await list();
    for (let i = 0; i < users.length; i++) {
      users[i].last_login_time = moment(users[i].last_login_time).format(
        "YYYY-MM-DD HH:mm:ss"
      );
    }
    this.users = users;
    }
  },
  async created() {
    this.getUsers();
    this.awilableRoles = await rolesList();
  },
  data() {
    return {
      users: null,
      awilableRoles: [],
    };
  },
};
</script>
