<template>
  <Dialog
    header="Edit user"
    v-model:visible="displayModal"
    :breakpoints="{ '960px': '75vw', '640px': '90vw' }"
    :style="{ width: '50vw' }"
    :modal="true"
    @hide="closeModal()"
  >
    <div class="test">
      <label>Id - {{ id }}</label>
      <label>Username</label>
      <InputText
        type="text"
        placeholder="Username"
        v-model="username"
      ></InputText>
      <label>Email</label>
      <InputText type="text" placeholder="Email" v-model="email"></InputText>
      <label>Password</label>
      <InputText
        type="password"
        placeholder="Password"
        v-model="password"
      ></InputText>
      <label>Confirm password</label>
      <InputText
        type="password"
        placeholder="Password"
        v-model="passwordConfirm"
      ></InputText>
      <label>Roles</label>

      <Button
        type="button"
        label="Add role"
        @click="openRole"
        aria-haspopup="true"
        aria-controls="overlay_menu"
      />
      <Menu id="overlay_menu" ref="menu" :model="awilableRoles" :popup="true" />

      <div v-for="role in rolesEdit" :key="role">
        <Chip removable @remove="removeRole(role)">
          {{ role }}
        </Chip>
      </div>
    </div>
    <template #footer>
      <Button label="Yes" icon="pi pi-check" @click="editData" autofocus />
    </template>
  </Dialog>
  <Toast />
</template>

<script>
import Button from "primevue/button";
import Dialog from "primevue/dialog";
import InputText from "primevue/inputtext";
import Toast from "primevue/toast";
import Chip from "primevue/chip";
import Menu from "primevue/menu";

import axios from "axios";

export default {
  data() {
    return {
      displayModal: false,
      id: null,
      username: "",
      email: "",
      password: "",
      passwordConfirm: "",
      rolesEdit: [],
      awilableRoles: [],
    };
  },
  components: {
    Dialog,
    Button,
    InputText,
    Toast,
    Chip,
    Menu,
  },
  methods: {
    openModal({ ...data }, roles) {
      this.displayModal = true;
      this.id = data.id;
      this.username = data.username;
      this.email = data.email;
      this.rolesEdit = data.roles.filter((role) => role);

      roles.forEach((role) => {
        this.awilableRoles.push({
          label: role.name,
          command: () => {
            this.addRole(role.name);
          },
        });
      });
    },
    closeModal() {
      this.displayModal = false;
      this.awilableRoles = [];
    },
    editData() {
      if (this.password !== this.passwordConfirm) {
        this.$toast.add({
          severity: "warn",
          summary: "Password does not match",
          life: 5000,
        });
      } else {
  

        axios
          .post("https://api-nules-web.sded.cf/user/edit", {
            id: this.id,
            username: this.username,
            password: this.password,
            email: this.email,
            roles: this.rolesEdit,
          })
          .catch((error) => {
            error.response.data.errors.forEach((error) => {
              this.$toast.add({
                severity: "error",
                summary: error.msg,
                life: 5000,
              });
            });
          })
          this.$parent.getUsers();
          this.closeModal();
      }
    },
    openRole(event) {
      this.$refs.menu.toggle(event);
    },
    addRole(name) {
      this.rolesEdit.push(name);
      console.log(this.rolesEdit);
    },
    removeRole(remove) {
      this.rolesEdit = this.rolesEdit.filter((role) => role !== remove);
      console.log(this.rolesEdit);
    },
  },
};
</script>

<style>
.test {
  display: flex;
  flex-direction: column;
}
</style>
