<template>
        <Dialog header="Confirmation" v-model:visible="displayModal" :style="{width: '350px'}" :modal="true">
            <div class="confirmation-content">
                <i class="pi pi-exclamation-triangle mr-3" style="font-size: 2rem" />
                <span>Are you sure you want to proceed?</span>
            </div>
            <template #footer>
                <Button label="No" icon="pi pi-times" @click="closeModal" class="p-button-text"/>
                <Button label="Yes" icon="pi pi-check" @click="closeModal" class="p-button-text" autofocus />
            </template>
        </Dialog>
</template>

<script>
import Button from "primevue/button";
import Dialog from "primevue/dialog";

export default {
  components: {
    Dialog,
    Button,
  },
  methods: {
    openModal(username) {
      this.displayModal = true;
      this.username = username;
    },
    closeModal() {
      this.displayModal = false;
    },
    deleteUser() {
      console.log("delete user" + this.username);
    },
  },
  data() {
    return {
      displayModal: false,
      username : null
    };
  },
};
</script>

